.slider {
  background-color: #f7f7f8;
  padding-bottom: 24px;
  position: relative;
  height: 100%;

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 120px;
    margin: 0 auto;

    @include tablet {
      flex-direction: column-reverse;
      gap: 20px;
    }
  }

  &__card {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      background-color: #60c0c3;
      width: 30%;
      height: 100%;
      z-index: -1;
      right: 0;
      top: 0;
      @include tablet {
        width: 100%;
        height: 30%;
      }
      @include mobile {
        height: 15%;
      }
    }
  }

  &__info-icons {
    display: flex;
  }

  &__info-text {
    margin: 16px 0;
    font-weight: 600;
    font-size: 33px;
    line-height: 116%;
    color: #003154;
    max-width: 486px;
  }

  &__info-p {
    font-weight: 400;
    font-size: 18px;
    line-height: 162%;
    color: #404040;
  }

  &__info-btn {
    margin-top: 56px;
    font-size: 15px;
    line-height: 148%;
    color: #003154;
    padding: 12px 59px;
    border: 1px solid #003154;
    border-radius: 2px;
    background-color: #f7f7f8;
    cursor: pointer;
  }


}



.info-icons {
  &__item {
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
}

.slider__pagination {

  @include tablet {
    top: 94%;
  }
}


.swiper-pagination-horizontal.swiper-pagination-bullets.swiper-pagination-bullets-dynamic{
  @include tablet{
    left: 75%

  }
}
