.customer {
  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    margin: 0 auto;
    align-items: flex-end;
    gap: 24px;
    margin: 64px 0;
  }

  &__item {
    z-index: 5;
    padding: 2rem;
    position: relative;
    &:not(:first-child){
      &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 77%;
        background-color: #f2f6fb;
        border-radius: 2px;
        bottom: 0;
        left: 0;
        z-index: -1;

      }
    }

  }
}

.cus-itme {
  &__title {
    font-weight: 600;
    font-size: 13px;
    line-height: 140%;
    color: #888888;
    margin: 8px auto;
  }

  &__bg {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
    max-width: 320px;
    margin-top: 32px;
  }
  &__bg1 {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
    max-width: 282px;
  }

  &__p {
    font-weight: 400;

    font-size: 15px;
    line-height: 174%;
    color: #404040;
    margin: 16px 0 40px 0;
    max-width: 320px;
  }
  &__p1 {
    font-weight: 400;

    font-size: 15px;
    line-height: 174%;
    color: #404040;
    margin: 16px  0 032px  0;
    max-width: 282px;
  }

  &__img {
  }

  &__btn {
    background: #003154;
    border-radius: 2px;
    padding: 12px 32px;
    font-weight: 400;
    font-size: 15px;
    line-height: 148%;

   a{
    color: #ffffff;

   }
  }
}
