.products {
  margin: 80px 0;

  &__container {
    //margin: 0 auto;
  }

  &__item-bg {
    margin-right: 100px;
  }

  &__title {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
    margin-bottom: 24px;
  }

  &__txte-title {
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 40px;
    margin: 0 auto;

    @include tablet {
      //flex-direction: column;
    }
  }

  &__item {
    &:not(:first-child) {
      border-left: 1px solid #e1e1e1;
    }
  }

  &__text {
    margin-left: 16px;
    max-width: 184px;
    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }
}
