.catalog {
  margin-top: 80px;
  margin-bottom: 64px;
  background-image: url(../img/catalog.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  position: relative;
  //&__container {
  //  display: flex;
  //}
  &__info {
    padding: 40px;
    max-width: 472px;
    background: #f2f6fb;
    border-radius: 2px;
    position: absolute;
    bottom: 48px;
    @include small-tablet {
      left: auto;
      margin-right: 10%;
    }
  }

  &__info-title {
    color: #003154;
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    margin-bottom: 16px;
  }

  &__info-p {
    color: #404040;
    font-weight: 400;
    font-size: 15px;
    line-height: 174%;
    margin-bottom: 40px;
  }

  &__info-btn {
    background: #003154;
    border-radius: 2px;
    padding: 12px 32px;
    a {
      color: #ffffff;
    }
  }
}
