.quality {
  display: flex;
  background-color: #f2f6fb;
  align-items: center;
  @include tablet{
    flex-direction: column;
  }

  &__img {
    max-width: 50%;
    @include tablet {
      max-width: 100%;

    }
  }

  &__info {

    margin-left: 80px;
    @include tablet{
      margin-left: 16px;
    }
  }

  &__info-title {
    font-weight: 600;
    font-size: 13px;
    line-height: 140%;
    color: #888888;
  }

  &__info-bg {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    max-width: 418px;
    color: #003154;
    margin: 8px 0 16px 0;
  }

  &__info-p {
    max-width: 418px;
    font-weight: 400;
    font-size: 15px;
    line-height: 174%;
    color: #404040;
  }

  &__info-link {
    margin-top: 32px;
    margin-bottom: 65px;
    cursor: pointer;
    position: relative;
   
  }
}

