.footer {
  background-color: #003154;
  position: relative;
  padding-bottom: 72px;

  &__memu-block {
    padding: 72px 0 180px 0;
    display: flex;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
      padding-bottom:0px;
    }
    &::after{
      content: "";
      width: 100%;
      height: 100%;
      left: 0;
      border-bottom: 1px solid #174463;
      bottom: 35%;
      position: absolute;
      @include tablet{
        display: none;
      }
    }

  }

  &__memu-items {
    display: flex;
    justify-content: space-between;
    width: 50%;
    border-bottom: 1px solid #174463;
    @include small-tablet{
      width: 100%;
      flex-direction: column;
    }



  }

  &__memu-item {
    display: block;
    max-width: 200px;
    font-weight: 400;
    font-size: 14px;
    line-height: 158%;
    color: #eaeaea;
    margin-bottom: 14px;
    align-items: center;
    span{
      margin-left: 6px;
      padding-top: 5px;
    }
    @include small-tablet {
      display: none;
    }
  }

  &__memu-item-bg {
    font-weight: 600;
    font-size: 18px;
    line-height: 152%;
    color: #ffffff;
    padding-bottom: 24px;
    @include small-tablet{
      border-bottom: 1px solid #174463;
      padding: 12px 0;

    }
  }

  &__memu-item-bottom {
    position: absolute;
    top: 333px;
    left: 0;
    width: 100%;
    height: 100%;
    @include small-tablet{
      position: relative;
      top: 0;
    }
  }
  &__memu-items1 {
    position: relative;
  }
  &__memu-items2 {
    position: relative;
  }

  &__memu-items3 {
  }

  &__memu-items4 {
  }

  &__memu--bg {
  }

  &__block1 {
    padding-top: 88px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__phon {
    margin-left: auto;
    @include small-tablet {
      margin: 20px auto;
    }
  }

  &__phon-til {
    font-weight: 600;
    font-size: 21px;
    line-height: 120%;
    a {
      color: #ffffff;
    }
  }

  &__phon-text {
    font-weight: 400;
    font-size: 11px;
    line-height: 158%;
    color: #eaeaea;
  }

  &__icons {
    margin: 0 70px;
    @include small-tablet {
      margin: 0 auto;
    }
    display: flex;
    align-items: center;
  }
  &__icons-item {
    &:not(:first-child) {
      margin-left: 8px;
    }
  }

  &__block2 {
    margin: 24px auto;
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }

  &__block1-item {
    font-weight: 400;
    font-size: 12px;
    line-height: 158%;
    color: #eaeaea;
    @include tablet {
      max-width: 288px;
      text-align: center;
      margin: 16px auto;
      &:last-child {
        margin-top: 0;
      }
    }
  }
  &__block3 {
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }

  &__block3-items1 {
    display: flex;
    gap: 24px;
    @include tablet {
      flex-direction: column;
      gap: 14px;
      margin-bottom: 24px;
    }
  }

  &__block3-item {
    font-weight: 400;
    font-size: 12px;
    line-height: 158%;
    color: #eaeaea;
    @include tablet {
      margin: 0 auto;
    }
  }

  &__block3-items2 {
  }

  &__block3-items2-item1 {
    max-width: 500px;
    color: #d0d0d0;
    font-weight: 400;
    font-size: 10px;
    line-height: 158%;
    @include tablet {
      margin: 0 auto;
      text-align: center;
      max-width: 288px;
    }
  }

  &__block3-items2-item2 {
    margin-top: 8px;
    max-width: 500px;
    color: #d0d0d0;
    font-weight: 400;
    font-size: 10px;
    line-height: 158%;
    @include tablet {
      margin: 8px auto;
      max-width: 288px;
      text-align: center;
      margin-bottom: 32px;
    }
  }
  &__top{
    @include small-tablet {
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      position: relative;
      cursor: pointer;
      &::before {
        position: absolute;
        content: "";
        background-image: url(../img/footer-top.svg);
        background-size: 15px 14px;
        background-position: 53%;
        background-repeat: no-repeat;
        width: 40px;
        height: 40px;
        right: 0;
        top: 0;
        transform: rotate(0deg);
        transition: all 0.3s;
      }

    }
  }
  &__bottom{
    //display: block;
    //position: static;
    //@include tablet{
    //  display: none;
    //}

  }
}



.footer__top.active {
  &::before {
    transform: rotate(180deg);
  }
}
















