.company {
  padding: 80px 0;
  background-color: #003154;

  &__text {
    text-align: center;
    font-weight: 400;
    font-size: 23px;
    line-height: 130%;
    color: #ffffff;
    max-width: 788px;
    margin: 0 auto;
  }

  &__numbers {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 86px;
    margin: 0 auto;
    margin-top: 101px;
    align-items: center;
  }

  &__numbers-item {
    position: relative;
    z-index: 3;

    &::after {
      content: "";
      background-image: url(../img/company1.svg);
      background-repeat: no-repeat;
      background-size: contain;

      z-index: -1;
      width: 100%;
      height: 100%;
      position: absolute;
      top: -21%;
      left: -8%;
      @include tablet{
        background-position: 027%;
      }
    }
    &:nth-child(2) {
      &::after {
        content: "";
        background-image: url(../img/company2.svg);
        background-repeat: no-repeat;
        background-size: contain;
        z-index: -1;
        width: 100%;
        height: 100%;
        position: absolute;
        top: -8px;
        left: -17px;
      }
    }
    &:nth-child(3) {
      &::after {
        content: "";
        background-image: url(../img/company3.svg);
        background-repeat: no-repeat;
        background-size: contain;
        z-index: -1;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0px;
        top: -13px;
      }
    }
    &:nth-child(4) {
      &::after {
        content: "";
        background-image: url(../img/company4.svg);
        background-repeat: no-repeat;
        background-size: contain;
        z-index: -1;
        width: 100%;
        height: 100%;
        position: absolute;
        top: -8px;
        left: -17px;
      }
    }
  }

  &__numbers-item-bg {
    font-weight: 600;
    font-size: 51px;
    line-height: 110%;
    color: #ffffff;
    text-align: center;
    span {
      font-size: 21px;
    }
  }

  &__numbers-item-p {
    font-weight: 400;
    font-size: 15px;
    line-height: 148%;
    color: #ffffff;
    text-align: center;
  }
}
