.header {

  &__top {

    border-bottom: 1px solid #EAEAEA;
    @include big-desktop{
      padding: 0 16px ;
    }
    @include tablet{
      border: none;

    }
  }
  &__top-item{
    padding: 24px 0;
    align-items: center;
    display: flex;
    justify-content: space-between;
  }

  &__top-logo {
    display: flex;
    img{
      height: 100%;

    }
    span {
      font-weight: 400;
      font-size: 11px;
      line-height: 158%;
      font-feature-settings: "pnum" on, "lnum" on;
      color: #8f9090;
      max-width: 360px;
      margin-left: 24px;

      @include small-tablet{
        display: none;
      }

    }
  }
  &__top-search {
    margin-left: 20px;
    @include tablet{
      margin-left: auto;
    }
  }


  &__bottom {
    padding: 16px 0;
  }
}

.top-nav {
  display: flex;
  margin-left: auto;


  &__list {
    display: flex;
    @include tablet{
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    @include tablet{

      border-bottom: 1px solid #EAEAEA;
      padding: 23px;

    }
    &:not(:last-child){
      margin-right: 32px;
    }
    span{
      margin-left: 6.9px;
    }
  }

  &__link {

  }

  @include tablet{
    padding-top: 25px;
    position: fixed;
    left: 0px;
    top: 80px;
    overflow-y: auto;
    background: #fbfbfd;
    width: 100%;
    height: auto;
    z-index: 500;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibiliy 0.3s ease-in-out;
  }
}

.bottom-nav {
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include tablet{
      display: -webkit-box;
      flex-direction: row;
      gap: 10px;
    }
  }

  &__item {

  }

  &__link {
   @include tablet{
    align-items: center;
    padding: 8px 12px;
    background: #fbfbff;
    border-radius: 4px;
   }

  }


  @include tablet{


  }

}

.burger{
  max-width: 17.63px;
  max-height: 15.13px;
display: none;
  margin-left: 20px;
  @include burger;
  @include tablet{
    display: block;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transition: opacity .5s ease, visibility 2s ease;

}



.item {

  &__top {
    position: relative;
    cursor: pointer;
    &::before {
      position: absolute;
      content: "";
      background-image: url(../img/header.png);
      background-size: 11px 8px;
      background-position: 53%;
      background-repeat: no-repeat;
      width: 40px;
      height: 40px;
      right: -30px;
      top:-8px;
      transform: rotate(0deg);
      transition: all 0.3s;
      @include tablet{
        background-image: none;
    }
    }
  }

  &__bottom {
    position: absolute;
    display: none;
    top: 25%;
    left: 5%;

    height: auto;
    z-index: 500;
    transition: opacity 0.3s ease-in-out, visibiliy 0.3s ease-in-out;
  }
}


.item__top.active{
  &::before {
    transform: rotate(180deg);
  }
}
