.brands {
  margin: 64px 0;

  &__title {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
    margin-bottom: 40px;
  }

  &__slider-container {
  }

  &__card {
    padding: 30px;
    border-left: 1px solid #E1E1E1;
    min-height: auto;
    color: #111111;
    font-size: 16px;
    margin-right: 2.5%;
    margin-bottom: 2.5%;

    @include  small-tablet{
      width: 100%;
      flex-shrink: 0;
      margin: 0 ;

    }
  }
  &__card-item {
    padding: 0 16px;
  }

  &__img {
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 158%;
    color: #404040;
    max-width: 242px;
  }

  &__pagination {
  }
}






/* swiper-bullets styling */

.slider-container {

	position: relative;
	margin: 0 auto;
	overflow: hidden;
	width: 100%;
	.swiper-wrapper {
		justify-content: space-between;
		flex-wrap: wrap;
    @include small-tablet{
      justify-content: initial;
			flex-wrap: nowrap;
    }
	}
}



