.wrapper {
	width: 50%;
	align-items: center;
	margin-left: 200px;
	margin-top: 15px;
}
.essam__item {
	border-bottom: 1px solid rgba(52, 96, 174, 0.5);
	padding-bottom: 15px;
	margin-top: 20px;
}
.essam__title {
	text-transform: uppercase;
	color: rgb(8, 121, 23);
	font-size: 25px;
	margin: 15px 0;
	position: relative;
	cursor: pointer;
	&::before {
		position: absolute;
		content: "";
		width: 25px;
		height: 2px;
		background-color: #3460AE;
		right: 0;
		top: 9px;
		transition: all 0.3s;
		transform: rotate(90deg);
	}
	&::after {
		position: absolute;
		content: "";
		width: 25px;
		height: 2px;
		background-color: #3460AE;
		right: 0;
		top: 9px;
		transition: all 0.3s;
	}
}
.essam__text {
	max-width: 280px;
}
.essam__title.active {
	&::before {
		transform: rotate(-0deg);
	}
}
