.news {
  margin: 64px 0 80px 0;
  &__title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
    margin-top: 64px;
  }

  &__title-1 {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
  }

  &__title-2 {
    font-weight: 400;
    font-size: 15px;
    line-height: 148%;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
      background-image: url(../img/quality.png);
      background-position: center;
      background-repeat: no-repeat;

      left: 55px;
      top: -3px;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 24px;
    margin: 0 auto;
  }

  &__item {
    background-color: #f7f7f8;
  }

  &__item-img {
    display: block;
  }
  &__item-info {
    padding: 24px;
  }

  &__item-title {
    color: #404040;
    font-weight: 400;
    font-size: 12px;
    line-height: 158%;
    margin-bottom: 16px;
    span {
      color: #8f9090;
      font-weight: 400;
      font-size: 12px;
      line-height: 158%;
    }
  }

  &__item-bg {
    color: #000103;
    font-weight: 600;
    font-size: 18px;
    line-height: 152%;
    margin-bottom: 8px;
  }

  &__item-p {
    color: #888888;
    font-weight: 400;
    font-size: 14px;
    line-height: 158%;
  }
}
